@tailwind base;
@tailwind components;
@tailwind utilities;

@mixin disableBlock() {
  display: none !important;
}

@mixin hAlign() {
  display: flex;
  align-items: center;
}


//$primary: #FC3F1D;
$primary: #EF3124;
$box-shadow:  0 1px 2px 0 rgba(60,64,67,.3), 0 1px 3px 1px rgba(60,64,67,.15);

.btn{
  &-primary {
    background-color: $primary !important;
    border-color: $primary !important;
  }
}

.text-primary {
  color: $primary ;
}

.icons {
  margin-right: 5px;
}
.icon{
  font-size: 1.5rem;
}


.menu :where(li:not(.menu-title):not(:empty)) > :where(:not(ul).active), .menu :where(li:not(.menu-title):not(:empty)) > :where(*:not(ul):active) {
  background-color: $primary ;
}
.grid-auto{
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  gap: 1rem;
}

.dropdown-close {
  opacity: 0 !important;
}

@media (min-width: 600px) {
  .cards { grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));

  }
}
@media (min-width: 900px) {
  .cards { grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
}
