@use "../../../core/components/App/App";


@media screen and (max-width: 1024px) {
  .navbar, .web {
    @include App.disableBlock;
  }
  .app {
    padding: 2rem 1rem 5rem 1rem;
  }
}

@media screen and (min-width: 1024px) {
  .btm-nav, .mobile {
    @include App.disableBlock;
  }
  .app {
    padding: 1rem 4rem;
  }
}


